import React, { useEffect } from "react";
import Card from "../../UI/Card";
import styles from "../styles.module.css";
import { Link } from "react-router-dom";
import { FiCheckCircle } from "react-icons/fi";
import { useSelector } from "react-redux";
import { Accordion, Card as Cards, Button } from "react-bootstrap";
import Loader from "../Loader";

const GuestInfo = ({ history }) => {
  const returningGuest = useSelector((state) => state.returningGuest);
  const { loading, error, success, returning } = returningGuest;

  useEffect(() => {
    if (!success) {
      history.push("/returning");
    }
  }, [history, success]);

  return (
    <div className={styles.app}>
      <Card
        title="Welcome"
        content={`Hello ${
          returning && returning.fullname
        }! Kindly verify your information you provided`}
      />
      {loading ? (
        <Loader />
      ) : (
        <div className={styles.form}>
          {error && <div className="alert alert-danger">{error}</div>}
          <Accordion defaultActiveKey="0">
            <Cards>
              <Cards.Header>
                <Accordion.Toggle as={Button} variant="btn" eventKey="0">
                  Basic Information
                </Accordion.Toggle>
              </Cards.Header>
              <Accordion.Collapse eventKey="0">
                <Cards.Body>
                  <p>Name: {returning && returning.fullname}</p>
                  <p>Company: {returning && returning.company}</p>
                  <p>Email Address: {returning && returning.email}</p>
                  <p>Mobile: {returning && returning.mobile}</p>
                </Cards.Body>
              </Accordion.Collapse>
            </Cards>
          </Accordion>
          <div className={styles.formContainer}>
            <div className="col-md-6 mx-auto">
              <Link
                className={`btn btn-success form-control ${styles.expandBtn}`}
                to="/returning/laptop"
              >
                <FiCheckCircle /> Proceed
              </Link>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default GuestInfo;
