import React, { useState } from "react";
import Card from "../../UI/Card";
import styles from "../styles.module.css";
import { Link } from "react-router-dom";
import { FiArrowRightCircle, FiArrowLeftCircle } from "react-icons/fi";
import { useDispatch, useSelector } from "react-redux";
import { getReturningGuest } from "../../../actions/vmsActions";
import Loader from "../Loader";

const ReturningGuest = ({ history }) => {
  const [mobile, setMobile] = useState("");

  const dispatch = useDispatch();

  const submitHandler = (e) => {
    e.preventDefault();

    dispatch(getReturningGuest(mobile));
  };

  const returningGuest = useSelector((state) => state.returningGuest);
  const { loading, error, success } = returningGuest;

  if (success) {
    history.push("/returning/userInfo");
  }

  return (
    <div className={styles.app}>
      <Card title="Welcome" content="Welcome Back!" />
      {loading ? (
        <Loader />
      ) : (
        <div className={styles.form}>
          {error && <div className="alert alert-danger">{error}</div>}
          <form onSubmit={submitHandler}>
            <div className={styles.formContainer}>
              <input
                type="tel"
                className="form-control"
                onChange={(e) => setMobile(e.target.value)}
                placeholder="Mobile"
                value={mobile}
              />
            </div>
            <div className={styles.formContainer}>
              <div className="col-md-3 float-start">
                <Link
                  to="/"
                  className={`btn btn-dark form-control  ${styles.expandBtn}`}
                >
                  <FiArrowLeftCircle /> Back
                </Link>
              </div>
              <div className="col-md-3 float-end">
                <button
                  type="submit"
                  className={`btn btn-success form-control ${styles.expandBtn}`}
                  value="Next"
                >
                  <FiArrowRightCircle /> Next
                </button>
              </div>
            </div>
          </form>
        </div>
      )}
    </div>
  );
};

export default ReturningGuest;
