import React from "react";
import Card from "../../UI/Card";
import styles from "../styles.module.css";
import { Link } from "react-router-dom";
import { FiCheckCircle, FiEdit } from "react-icons/fi";
import { useDispatch, useSelector } from "react-redux";
import { addReturningGuest } from "../../../actions/vmsActions";
import { Accordion, Card as Cards, Button } from "react-bootstrap";
import Loader from "../Loader";
import { ADDRETURNING_GUEST_RESET } from "../../../constants/vmsConstants";

const RVerifyInfo = ({ history }) => {
  const returningGuest = useSelector((state) => state.returningGuest);
  const { returning } = returningGuest;
  const user = returning && returning._id;
  const fullname = returning && returning.fullname;
  const company = returning && returning.company;
  const email = returning && returning.email;
  const mobile = returning && returning.mobile;
  const laptop = localStorage.getItem("laptop");
  const lsn = localStorage.getItem("lsn");
  const host = localStorage.getItem("host");
  const purpose = localStorage.getItem("purpose");
  const appointment = localStorage.getItem("appointment");
  const photo = localStorage.getItem("photo");
  var timeIn = new Date().toLocaleString();
  var date = new Date().toJSON().slice(0, 10).replace(/-/g, "/");

  const dispatch = useDispatch();

  const submitHandler = (e) => {
    e.preventDefault();

    dispatch(
      addReturningGuest(
        user,
        laptop,
        lsn,
        host,
        purpose,
        appointment,
        photo,
        timeIn,
        date
      )
    );
  };

  const aGuest = useSelector((state) => state.aGuest);
  const { loading, error, success } = aGuest;
  if (success) {
    console.log("User Added");
    localStorage.removeItem("laptop");
    localStorage.removeItem("lsn");
    localStorage.removeItem("host");
    localStorage.removeItem("purpose");
    localStorage.removeItem("appointment");
    localStorage.removeItem("photo");
    dispatch({ type: ADDRETURNING_GUEST_RESET });
    history.push("/success");
  }
  return (
    <div className={styles.app}>
      <Card
        title="Welcome"
        content={`Hello ${
          fullname && fullname
        }! Kindly verify the information you provided`}
      />
      {loading ? (
        <Loader />
      ) : (
        <div className={styles.form}>
          {error && <div className="alert alert-danger">{error}</div>}
          <Accordion defaultActiveKey="0">
            <Cards>
              <Cards.Header>
                <Accordion.Toggle as={Button} variant="btn" eventKey="0">
                  Basic Information
                </Accordion.Toggle>
              </Cards.Header>
              <Accordion.Collapse eventKey="0">
                <Cards.Body>
                  <p>Name: {fullname}</p>
                  <p>Company: {company}</p>
                  <p>Email Address: {email}</p>
                  <p>Mobile: {mobile}</p>
                </Cards.Body>
              </Accordion.Collapse>
            </Cards>
            <Cards>
              <Cards.Header>
                <Accordion.Toggle as={Button} variant="btn" eventKey="1">
                  Laptop Information
                </Accordion.Toggle>
              </Cards.Header>
              <Accordion.Collapse eventKey="1">
                <Cards.Body>
                  <p>Laptop: {laptop}</p>
                  <p>Laptop Serial Number: {lsn}</p>
                  <Link to="/returning/laptop" className="btn btn-success">
                    <FiEdit /> Edit
                  </Link>
                </Cards.Body>
              </Accordion.Collapse>
            </Cards>
            <Cards>
              <Cards.Header>
                <Accordion.Toggle as={Button} variant="btn" eventKey="2">
                  Host Information
                </Accordion.Toggle>
              </Cards.Header>
              <Accordion.Collapse eventKey="2">
                <Cards.Body>
                  <p>Host: {host}</p>
                  <p>Purpose of Visit: {purpose}</p>
                  <p>Do you have an appointment: {appointment}</p>
                  <Link to="/returning/host" className="btn btn-success">
                    <FiEdit /> Edit
                  </Link>
                </Cards.Body>
              </Accordion.Collapse>
            </Cards>
          </Accordion>
          <div className={styles.formContainer}>
            <div className="col-md-6 mx-auto">
              <button
                type="button"
                className={`btn btn-success form-control ${styles.expandBtn}`}
                onClick={submitHandler}
              >
                <FiCheckCircle /> Submit Information
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default RVerifyInfo;
