import React, { useState } from "react";
import Card from "../../UI/Card";
import styles from "../styles.module.css";
import { Link } from "react-router-dom";
import { FiArrowRightCircle, FiArrowLeftCircle } from "react-icons/fi";

const NewGuest = ({ history }) => {
  const n = localStorage.getItem("fullname");
  const c = localStorage.getItem("company");
  const e = localStorage.getItem("email");
  const m = localStorage.getItem("mobile");

  const [fullname, setFullName] = useState(n ? n : "");
  const [company, setCompany] = useState(c ? c : "");
  const [email, setEmail] = useState(e ? e : "");
  const [mobile, setMobile] = useState(m ? m : "");

  const submitHandler = (e) => {
    e.preventDefault();
    localStorage.setItem("fullname", fullname);
    localStorage.setItem("company", company);
    localStorage.setItem("email", email);
    localStorage.setItem("mobile", mobile);
    history.push("/laptop");
  };

  return (
    <div className={styles.app}>
      <Card title="Welcome" content="We need some details" />
      <div className={styles.form}>
        <form onSubmit={submitHandler}>
          <div className={styles.formContainer}>
            <input
              type="text"
              className="form-control"
              onChange={(e) => setFullName(e.target.value)}
              placeholder="First Name"
              value={fullname}
            />
          </div>
          <div className={styles.formContainer}>
            <input
              type="text"
              className="form-control"
              onChange={(e) => setCompany(e.target.value)}
              placeholder="Company"
              value={company}
            />
          </div>
          <div className={styles.formContainer}>
            <input
              type="text"
              className="form-control"
              onChange={(e) => setEmail(e.target.value)}
              placeholder="Email Address"
              value={email}
            />
          </div>
          <div className={styles.formContainer}>
            <input
              type="tel"
              className="form-control"
              onChange={(e) => setMobile(e.target.value)}
              placeholder="Mobile"
              value={mobile}
            />
          </div>
          <div className={styles.formContainer}>
            <div className="col-md-3 float-start">
              <Link
                to="/"
                className={`btn btn-dark form-control  ${styles.expandBtn}`}
              >
                <FiArrowLeftCircle /> Back
              </Link>
            </div>
            <div className="col-md-3 float-end">
              <button
                type="submit"
                className={`btn btn-success form-control ${styles.expandBtn}`}
                value="Next"
              >
                <FiArrowRightCircle /> Next
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default NewGuest;
