import { LOG_FAIL, LOG_REQUEST, LOG_SUCCESS } from "../constants/logConstants";

export const logReducer = (state = { logs: {}, newGuest: [] }, action) => {
  switch (action.type) {
    case LOG_REQUEST:
      return { loading: true };
    case LOG_SUCCESS:
      return {
        loading: false,
        logs: action.payload,
        newGuest: action.payload.newGuest,
        success: true,
      };
    case LOG_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};
