import React from "react";
import Card from "../../UI/Card";
import styles from "../styles.module.css";
import { Link } from "react-router-dom";
import { FiCheckCircle, FiEdit } from "react-icons/fi";
import { useDispatch, useSelector } from "react-redux";
import { addNewGuest } from "../../../actions/vmsActions";
import { Accordion, Card as Cards, Button } from "react-bootstrap";
import Loader from "../Loader";
import { FIRST_TIME_GUEST_RESET } from "../../../constants/vmsConstants";

const VerifyInfo = ({ history }) => {
  const fullname = localStorage.getItem("fullname");
  const company = localStorage.getItem("company");
  const email = localStorage.getItem("email");
  const mobile = localStorage.getItem("mobile");
  const laptop = localStorage.getItem("laptop");
  const lsn = localStorage.getItem("lsn");
  const host = localStorage.getItem("host");
  const purpose = localStorage.getItem("purpose");
  const appointment = localStorage.getItem("appointment");
  const photo = localStorage.getItem("photo");
  var timeIn = new Date().toLocaleString();
  var date = new Date().toJSON().slice(0, 10).replace(/-/g, "/");
  const dispatch = useDispatch();

  const submitHandler = (e) => {
    e.preventDefault();

    dispatch(
      addNewGuest(
        fullname,
        company,
        email,
        mobile,
        laptop,
        lsn,
        host,
        purpose,
        appointment,
        photo,
        timeIn,
        date
      )
    );
  };

  const newGuest = useSelector((state) => state.newGuest);
  const { loading, error, success } = newGuest;
  if (success) {
    console.log("User Added");
    localStorage.removeItem("fullname");
    localStorage.removeItem("company");
    localStorage.removeItem("email");
    localStorage.removeItem("mobile");
    localStorage.removeItem("laptop");
    localStorage.removeItem("lsn");
    localStorage.removeItem("host");
    localStorage.removeItem("purpose");
    localStorage.removeItem("appointment");
    localStorage.removeItem("photo");
    dispatch({ type: FIRST_TIME_GUEST_RESET });
    history.push("/success");
  }
  return (
    <div className={styles.app}>
      <Card
        title="Welcome"
        content={`Hello ${
          fullname && fullname
        }! Kindly verify the information you provided`}
      />
      {loading ? (
        <Loader />
      ) : (
        <div className={styles.form}>
          {error && <div className="alert alert-danger">{error}</div>}
          <Accordion defaultActiveKey="0">
            <Cards>
              <Cards.Header>
                <Accordion.Toggle as={Button} variant="btn" eventKey="0">
                  Basic Information
                </Accordion.Toggle>
              </Cards.Header>
              <Accordion.Collapse eventKey="0">
                <Cards.Body>
                  <p>Name: {fullname}</p>
                  <p>Company: {company}</p>
                  <p>Email Address: {email}</p>
                  <p>Mobile: {mobile}</p>
                  <Link to="/newGuest" className="btn btn-success">
                    <FiEdit /> Edit
                  </Link>
                </Cards.Body>
              </Accordion.Collapse>
            </Cards>
            <Cards>
              <Cards.Header>
                <Accordion.Toggle as={Button} variant="btn" eventKey="1">
                  Laptop Information
                </Accordion.Toggle>
              </Cards.Header>
              <Accordion.Collapse eventKey="1">
                <Cards.Body>
                  <p>Laptop: {laptop}</p>
                  <p>Laptop Serial Number: {lsn}</p>
                  <Link to="/laptop" className="btn btn-success">
                    <FiEdit /> Edit
                  </Link>
                </Cards.Body>
              </Accordion.Collapse>
            </Cards>
            <Cards>
              <Cards.Header>
                <Accordion.Toggle as={Button} variant="btn" eventKey="2">
                  Host Information
                </Accordion.Toggle>
              </Cards.Header>
              <Accordion.Collapse eventKey="2">
                <Cards.Body>
                  <p>Host: {host}</p>
                  <p>Purpose of Visit: {purpose}</p>
                  <p>Do you have an appointment: {appointment}</p>
                  <Link to="/host" className="btn btn-success">
                    <FiEdit /> Edit
                  </Link>
                </Cards.Body>
              </Accordion.Collapse>
            </Cards>
          </Accordion>
          <div className={styles.formContainer}>
            <div className="col-md-6 mx-auto">
              <button
                type="button"
                className={`btn btn-success form-control ${styles.expandBtn}`}
                onClick={submitHandler}
              >
                <FiCheckCircle /> Submit Information
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default VerifyInfo;
