import React, { useState } from "react";
import Card from "../../UI/Card";
import styles from "../styles.module.css";
import { Link } from "react-router-dom";
import { FiArrowRightCircle, FiArrowLeftCircle } from "react-icons/fi";

const LaptopScreen = ({ history }) => {
  const l = localStorage.getItem("laptop");
  const sn = localStorage.getItem("lsn");
  const [laptop, setLaptop] = useState(l ? l : "");
  const [lsn, setLSN] = useState(sn ? sn : "");

  const submitHandler = (e) => {
    e.preventDefault();
    localStorage.setItem("laptop", laptop);
    localStorage.setItem("lsn", lsn);
    history.push("/host");
  };

  return (
    <div className={styles.app}>
      <Card title="Welcome" content="Enter your Laptop Details" />
      <div className={styles.form}>
        <form onSubmit={submitHandler}>
          <div className={styles.formContainer}>
            <select
              type="text"
              className="form-control"
              onChange={(e) => setLaptop(e.target.value)}
              placeholder="Laptop"
            >
              <option>{l ? l : "Select....."}</option>
              <option value="Acer">Acer</option>
              <option value="Dell">Dell</option>
              <option value="HP">HP</option>
              <option value="Lenovo">Lenovo</option>
              <option value="Microsoft">Microsoft</option>
              <option value="MSI">MSI</option>
              <option value="Mac Book">Mac Book</option>
            </select>
          </div>
          <div className={styles.formContainer}>
            <input
              type="text"
              className="form-control"
              onChange={(e) => setLSN(e.target.value)}
              placeholder="Laptop Serial Number"
              value={lsn}
            />
          </div>

          <div className={styles.formContainer}>
            <div className="col-md-3 float-start">
              <Link
                to="/newGuest"
                className={`btn btn-dark form-control  ${styles.expandBtn}`}
              >
                <FiArrowLeftCircle /> Back
              </Link>
            </div>
            <div className="col-md-3 float-end">
              <button
                type="submit"
                className={`btn btn-success form-control ${styles.expandBtn}`}
                value="Next"
              >
                <FiArrowRightCircle /> Next
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default LaptopScreen;
