import React, { useEffect } from "react";
import Capsules from "./UI/Capsules";
import Navbar from "./Navbar";
import styles from "./styles.module.css";

import { useDispatch, useSelector } from "react-redux";
import { getlogs } from "../../../actions/logActions";

const NewGuestScreen = () => {
  const dispatch = useDispatch();

  const vmslogs = useSelector((state) => state.vmslogs);
  const { newGuest } = vmslogs;

  useEffect(() => {
    var date = new Date().toJSON().slice(0, 10).replace(/-/g, "/");
    dispatch(getlogs(date));

    //dispatch(getAdminDetails());
  }, [dispatch]);

  //   const vmslogs = useSelector((state) => state.vmslogs);
  //   const { logs } = vmslogs;
  return (
    <div>
      <Navbar />
      <div className={styles.contents}>
        <div className={styles.title}>
          <small className="alert alert-success">Pending Guests</small>
        </div>
        <div className={styles.cardGrid}>
          {newGuest && newGuest.map((item) => <Capsules user={item} />)}
        </div>
      </div>
    </div>
  );
};

export default NewGuestScreen;
