import React from "react";
import Card from "../../UI/Card";
import styles from "../styles.module.css";
import { Link } from "react-router-dom";
import { FiArrowRightCircle, FiArrowLeftCircle } from "react-icons/fi";
import Webcam from "react-webcam";

const RSnapshot = ({ history }) => {
  const videoConstraints = {
    width: 400,
    height: 400,
    facingMode: "user",
  };

  const webcamRef = React.useRef(null);

  const capture = React.useCallback(() => {
    const imageSrc = webcamRef.current.getScreenshot();
    localStorage.setItem("photo", imageSrc);
    history.push("/returning/verifyInfo");
  }, [webcamRef, history]);

  return (
    <div className={styles.app}>
      <Card title="Welcome" content="Take a Picture" />
      <div className={styles.form}>
        <form onSubmit={capture}>
          <div className={`${styles.formContainer} text-center`}>
            <Webcam
              audio={false}
              height={400}
              ref={webcamRef}
              screenshotFormat="image/jpeg"
              width={400}
              videoConstraints={videoConstraints}
            />
          </div>
          <div className={styles.formContainer}>
            <div className="col-md-3 float-start">
              <Link
                to="/returning/host"
                className={`btn btn-dark form-control  ${styles.expandBtn}`}
              >
                <FiArrowLeftCircle /> Back
              </Link>
            </div>
            <div className="col-md-3 float-end">
              <button
                type="submit"
                className={`btn btn-success form-control ${styles.expandBtn}`}
                value="Next"
              >
                <FiArrowRightCircle /> Next
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default RSnapshot;
